import styled from 'styled-components';

export const Wrapper = styled.div`
  width: 30px;
  height: 30px;
  border-radius: 100%;
  display: grid;
  place-items: center;

  svg {
    width: 60%;
    height: 60%;
    path,
    line {
      stroke: ${({ theme }) => theme.colors.mainDark};
    }
  }
`;
